import { h, render, Component } from 'preact';

import { config } from './cta.config.js'

import style from './style.scss';

const axios = require('axios');

class CtaAppTrain extends Component {

	state = {
		destination: '',
		predictions: '',
		error: ''
	};

	componentDidMount() {
		this.getNextTransit(this.props.stopId);
	}

	// Given a prdt and a arrT, return an integer representing the minutes difference between the two timestamps
	getDifferenceInMinutes = (arrT, prdt) => {
		return ((new Date(arrT)) - (new Date(prdt))) / 60000;
	};

	// Given the raw countdown (in minutes) from the CTA API, return the modified/filtered string for display
	getModifiedCountdown = countdown => {
		if(isNaN(countdown)){
			// TODO ERROR
		}else if(countdown <= 1){
			return "Due";
		}else{
			return countdown + " min";
		}
	};

	// Given the stop id, return a string representing the next transit(s) predicted
	getNextTransit = async stopId => {

		let response = await axios.get(`${config.PROXY_URL}${config.TRAIN_ROOT_URL}api/1.0/ttarrivals.aspx?key=${config.TRAIN_API_KEY}&stpid=${stopId}&outputType=JSON`);

		if(response && response.data && response.data.ctatt){

			if(response.data.ctatt.eta && 0 < response.data.ctatt.eta.length){

				console.log(response.data.ctatt.eta);

				let predictionValues = [];
				let predictions = response.data.ctatt.eta;
				predictions.forEach((prediction, index) => {
					predictionValues.push(this.getModifiedCountdown(this.getDifferenceInMinutes(prediction.arrT, prediction.prdt)));
				});

				this.setState({
					destination: response.data.ctatt.eta[0].destNm,
					predictions: predictionValues
				});

			}else if (response.data.ctatt.errNm && 0 < response.data.ctatt.errNm.length){
				this.setState({
					error: ("Blue Line: " + response.data.ctatt.errNm)
				});
			}else{
				// TODO ERROR
			}

		}else{
			// TODO ERROR
		}
	};

	render() {

		if(0 < this.state.predictions.length){

			let predictionsStr = '';
			this.state.predictions.forEach((prediction, index) => {
				if(index !== 0){
					predictionsStr += prediction;
					if(index !== (this.state.predictions.length - 1)){
						predictionsStr += ', ';
					}
				}
			});

			return (

				<div className={[style.ctaAppChild, style.ctaAppChildTrain].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.state.destination}
						</div>
						<div className={style.ctaAppChildSmall}>
							Western Blue Line
						</div>
					</div>
					<div className={style.ctaAppChildRight}>
						<div className={style.ctaAppChildLarge}>
							{this.state.predictions[0]}
						</div>
						<div className={style.ctaAppChildSmall}>
							{predictionsStr}
						</div>
					</div>
				</div>

			);
		}else if (0 < this.state.error.length){
			return (
				<div className={[style.ctaAppChild, style.ctaAppChildTrain].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.state.error}
						</div>
					</div>
				</div>
			);
		}else {
			return (
				<div className={[style.ctaAppChild, style.ctaAppChildTrain].join(' ')}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							Loading Blue Line
						</div>
					</div>
				</div>
			);
		}
	}
}

class CtaApp extends Component {

	state = {
		direction: '',
		destination: '',
		predictions: '',
		error: ''
	};

	componentDidMount() {
		this.getNextTransit(this.props.route, this.props.stopId);
	}

	// Given the raw direction from the CTA API, return the modified/filtered string for display
	getModifiedDirection = direction => {
		return direction.replace('bound', '');
	};

	// Given the raw countdown string from the CTA API, return the modified/filtered string for display
	getModifiedCountdown = countdown => {
		if(isNaN(countdown)){
			if(countdown.toUpperCase().includes('DUE')){
				return 'Due';
			}else if(countdown.toUpperCase().includes('DLY')){
				return 'Delayed';
			}else{
				return countdown;
			}
		}else{
			return countdown + " min"
		}
	};

	// Given the route and stop number, return a string representing the next transit(s) predicted
	getNextTransit = async (route, stopId) => {

		let response = await axios.get(`${config.PROXY_URL}${config.BUS_ROOT_URL}bustime/api/v2/getpredictions?key=${config.BUS_API_KEY}&format=json&rt=${route}&stpid=${stopId}`);

		// TODO Handle response.data['bustime-response'].error -> "msg": "No arrival times"
		if(response && response.data && response.data['bustime-response']){
			if(response.data['bustime-response'].prd && 0 < response.data['bustime-response'].prd.length){

				console.log(response);

				let predictionValues = [];
				let predictions = response.data['bustime-response'].prd;
				predictions.forEach((prediction, index) => {
					predictionValues.push(this.getModifiedCountdown(prediction.prdctdn));
				});

				this.setState({
					direction: this.getModifiedDirection(response.data['bustime-response'].prd[0].rtdir),
					destination: response.data['bustime-response'].prd[0].des,
					predictions: predictionValues
				});

			}else if (response.data['bustime-response'].error && 0 < response.data['bustime-response'].error.length){
				this.setState({
					error: (this.props.route + ": " + response.data['bustime-response'].error[0].msg)
				});
			}else{
				// TODO ERROR
			}
		}else{
			// TODO ERROR
		}
	};

	render() {

		if(0 < this.state.predictions.length){

			let predictionsStr = '';
			this.state.predictions.forEach((prediction, index) => {
				if(index !== 0){
					predictionsStr += prediction;
					if(index !== (this.state.predictions.length - 1)){
						predictionsStr += ', ';
					}
				}
			});

			return (

				<div className={style.ctaAppChild}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.props.route} to {this.state.destination}
						</div>
						<div className={style.ctaAppChildSmall}>
							{this.state.direction}
						</div>
					</div>
					<div className={style.ctaAppChildRight}>
						<div className={style.ctaAppChildLarge}>
							{this.state.predictions[0]}
						</div>
						<div className={style.ctaAppChildSmall}>
							{predictionsStr}
						</div>
					</div>
				</div>

			);
		}else if (0 < this.state.error.length){
			return (
				<div className={style.ctaAppChild}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							{this.state.error}
						</div>
					</div>
				</div>
			);
		}else {
			return (
				<div className={style.ctaAppChild}>
					<div className={style.ctaAppChildLeft}>
						<div className={style.ctaAppChildLarge}>
							Loading {this.props.route} at {this.props.stopId}
						</div>
					</div>
				</div>
			);
		}
	}
}

export default class CtaAppParent extends Component {

	render() {

		let stops = [];
		config.STOPS.forEach((stop) => {
			if(stop.isTrain){
				stops.push(<CtaAppTrain stopId={stop.stopId} />);
			}else{
				stops.push(<CtaApp route={stop.route} stopId={stop.stopId} />);
			}
		});

		return (
			<div className={style.ctaApp}>

				{stops}

			</div>
		);
	}
}